<template>
  <div class="classic-search-page">
    <div class="page-bg">
      <div class="page-bg-image" />
      <div class="page-bg-gradient" />
    </div>
    <div class="search-inner">
      <div class="search-bar">
        <el-input
          v-model="inputQ"
          placeholder="输入关键词搜索"
          size="large"
          clearable
          class="input"
          @keyup.enter="doSearch"
        >
          <template #append>
            <el-button type="primary" @click="doSearch">搜索</el-button>
          </template>
        </el-input>
      </div>
      <div v-if="searched" class="result-header">
        <span>“{{ routeQuery }}” 共 {{ total }} 条</span>
      </div>
      <div v-if="loading" class="loading-wrap">
        <el-icon class="is-loading" :size="32"><Loading /></el-icon>
      </div>
      <div v-else-if="searched && results.length === 0" class="empty">暂无结果</div>
      <div v-else class="list-wrap">
        <router-link
          v-for="item in currentPageItems"
          :key="item.id"
          :to="`/classic/item/${item.id}`"
          class="item-card"
        >
          <p class="original content-font">{{ item.original_text }}</p>
          <p v-if="showTranslation && item.translation" class="translation">
            {{ item.translation }}
          </p>
          <div class="meta">
            <span v-if="showAuthor && item.author">{{ item.author }}</span>
            <span v-if="showDynasty && item.dynasty">{{ item.dynasty }}</span>
            <span v-if="item.source">{{ item.source }}</span>
          </div>
        </router-link>
      </div>
      <div v-if="totalPages > 1" class="pagination-wrap">
        <el-pagination
          v-model:current-page="currentPage"
          :page-size="pageSize"
          :total="total"
          layout="prev, pager, next"
          @current-change="() => {}"
        />
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref, computed, watch, onMounted } from 'vue'
import { useRoute } from 'vue-router'
import { Loading } from '@element-plus/icons-vue'
import { search } from '@/api/classic'
import type { ClassicItem } from '@/types/classic'

const route = useRoute()
const inputQ = ref('')
const results = ref<ClassicItem[]>([])
const loading = ref(false)
const searched = ref(false)
const showTranslation = ref(true)
const showAuthor = ref(true)
const showDynasty = ref(true)
const pageSize = 20
const currentPage = ref(1)

const routeQuery = computed(() => (route.query.q as string) ?? '')

const total = computed(() => results.value.length)
const totalPages = computed(() => Math.ceil(total.value / pageSize) || 1)
const currentPageItems = computed(() => {
  const start = (currentPage.value - 1) * pageSize
  return results.value.slice(start, start + pageSize)
})

async function doSearch() {
  const q = (inputQ.value || routeQuery.value)?.trim()
  if (!q) return
  loading.value = true
  searched.value = true
  try {
    const list = await search(q, 'keyword')
    results.value = list ?? []
    currentPage.value = 1
  } finally {
    loading.value = false
  }
}

function loadFromRoute() {
  const q = routeQuery.value
  if (q) {
    inputQ.value = q
    doSearch()
  }
}

watch(() => route.query.q, loadFromRoute)
onMounted(loadFromRoute)
</script>

